const sliceCat = text => {
  let result = text
  const target = [
    ' ',
    '',
    ' ',
    '  (ES6):',
    '  ',
    ' ',
    '    ',
  ]
  const hasThisTitle = target.some(item => String(text).includes(item))
  if (hasThisTitle) {
    result = String(result).split(/[:|]/)[1]
  }
  return result
}

export { sliceCat }
